.wrap {
  position: relative;
  overflow: hidden;  
}

.page_content {
  max-width: 1180px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
}

.full_width {
  margin: 0 (-$mobile_side_margin);
}

a,
body,
